@import "colors";
@import "mixins";

html,
body {
  height: 100%; }
*::first-letter {
  text-transform: uppercase; }
.wrapper {
  display: flex;
  flex-direction: column;
  height: 100%;
  font-size: 100%;
  line-height: 1.3;
  font-family: 'calibri', Arial, sans-serif; }
.container {
  width: 1170px;
  margin: 0 auto;
  position: relative; }
.main-container {
  padding: 45px 3px;
  height: 100%;
  width: 100%;
  box-sizing: border-box; }
main {
  flex: 1 0 auto; }
footer {
  flex: 0 0 auto; }
a {
  display: inline-block;
  text-decoration: none;
  color: inherit; }
input {
  &[type=text] {
    &::-ms-clear, &::-ms-reveal {
      display: none;
      width: 0;
      height: 0; } }
  &[type="search"] {
    &::-webkit-search-decoration, &::-webkit-search-cancel-button, &::-webkit-search-results-button, &::-webkit-search-results-decoration {
      display: none; } } }
.map-link {
  position: relative;
  &::after {
    position: absolute;
    content: '';
    border-bottom: 1px solid #fff;
    bottom: 0;
    left: 0;
    width: 100%; } }
.section-title {
  font-size: 1em;
  font-weight: bold;
  text-transform: uppercase; }
.page-btn {
  display: inline-block;
  min-width: 130px;
  border: 1px solid $sky;
  padding: 7px 20px;
  text-align: center;
  color: rgb(52, 52, 52);
  background: rgba(#fff, 0.3);
  font-size: 1em;
  box-sizing: border-box;
  transition: all 0.3s ease;
  &:hover {
    background-color: $yellow;
    border-color: $yellow;
    color: $blue; } }
