.market-menu {
  .row {
    position: relative;
    text-align: center;
    margin: 0 auto; }
  &__item {
    height: 180px;
    display: flex;
    flex-direction: column;
    padding: 10px 5px 3px;
    outline: 1px solid #fff;
    box-sizing: border-box;
    @include transition(all .3s ease);
    &:hover, &:focus, &:active {
      background: #fff;
      .market-menu__title {
        color: inherit; } } }

  &__img {
    width: 80px;
    height: 80px;
    max-height: 80px;
    margin: 0 auto 5px;
    border-radius: 100%;
    border: 3px solid;
    border-color: inherit;
    display: flex;
    align-items: center;
    justify-content: center;
    background: #fff;
    flex: 1;
    img {
      max-width: 100%; } }
  &__title {
    font-size: 0.9em;
    color: #fff;
    text-transform: uppercase;
    line-height: 1.1;
    text-align: center;
    flex: 1;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    p {
      white-space: nowrap; } } }
